#foot {
  background-color: #FFFFFF;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  border-top: black 1px solid;
  border-bottom: black 1px solid;
}

#foot a {
  color: black;
}

#foot button {
  background-color: white;
  /* Green */
  border: #FCE181 1px solid;
  color: black;
  padding: 0px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  border-radius: 8px;
  transition-duration: 0.4s;
  width: 100px;
}

#foot button:hover {
  background-color: #FCE181;
  color: black;
}